import Vue from 'vue'
import VueRouter from 'vue-router'

const Login = () => import(/* webpackChunkName: "login_home_welcome" */ '../views/Login.vue')
const Home = () => import(/* webpackChunkName: "login_home_welcome" */ '../views/Home.vue')
const Welcome = () => import(/* webpackChunkName: "login_home_welcome" */ '../views/Home_main/Welcome.vue')
// import Login from '../views/Login.vue'
// import Home from '../views/Home.vue'
// import Welcome from '../views/Home_main/Welcome.vue'

const Users = () => import(/* webpackChunkName: "users_rights_roles" */ '../views/Home_main/user/Users.vue')
const Rights = () =>
  import(/* webpackChunkName: "users_rights_roles" */ '../views/Home_main/power/Rights.vue')
const Roles = () => import(/* webpackChunkName: "users_rights_roles" */ '../views/Home_main/power/Roles.vue')
// import Users from '../views/Home_main/user/Users.vue'
// import Rights from '../views/Home_main/power/Rights.vue'
// import Roles from '../views/Home_main/power/Roles.vue'

const Cate = () => import(/* webpackChunkName: "cate_params" */ '../views/Home_main/goods/Cate.vue')
const Params = () => import(/* webpackChunkName: "cate_params" */ '../views/Home_main/goods/Params.vue')
// import Cate from '../views/Home_main/goods/Cate.vue'
// import Params from '../views/Home_main/goods/Params.vue'

const GoodsList = () => import(/* webpackChunkName: "goodslist_add" */ '../views/Home_main/goods/List.vue')
const Add = () => import(/* webpackChunkName: "goodslist_add" */ '../views/Home_main/goods/Add.vue')
// import GoodsList from '../views/Home_main/goods/List.vue'
// import Add from '../views/Home_main/goods/Add.vue'

const Order = () => import(/* webpackChunkName: "order_report" */ '../views/Home_main/order/Order.vue')
const Report = () => import(/* webpackChunkName: "order_report" */ '../views/Home_main/report/Report.vue')
// import Order from '../views/Home_main/order/Order.vue'
// import Report from '../views/Home_main/report/Report.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/login' },
  { path: '/login', component: Login },
  {
    path: '/home',
    component: Home,
    redirect: '/welcome',
    children: [
      { path: '/welcome', component: Welcome },
      { path: '/users', component: Users },
      { path: '/rights', component: Rights },
      { path: '/roles', component: Roles },
      { path: '/categories', component: Cate },
      { path: '/params', component: Params },
      { path: '/goods', component: GoodsList },
      { path: '/goods/add', component: Add },
      { path: '/orders', component: Order },
      { path: '/reports', component: Report }
    ]
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

// 路由导航守卫
// to：将要跳转的路径
// from：从哪个路径跳转
// next：放行，next('/xxx')：强制跳转
router.beforeEach((to, from, next) => {
  // 除login外。访问所有页面之前，都需要验证会话存储中token是否存在才会放行
  // bug：没有让服务端校验token的真实性，即使token的值被修改，依然会放行
  if (to.path === '/login') return next()
  const tokenStr = window.sessionStorage.getItem('token')
  if (!tokenStr) return next('/login')
  next()
})

export default router
